.selectbox { text-align: center; margin-top: 5rem; }
.area { display: flex; flex-wrap: wrap;  justify-content: center; width: 65rem; margin: 0 auto; }
.area > li { flex: 11%; padding: 1rem 0; cursor: pointer; } 
.middle { margin: 0 1rem; height: 2rem; }
.ulList { padding: 5rem; display: flex; justify-content: left; flex-wrap: wrap; width: 80rem; height: 55rem; overflow: hidden; margin: 0 auto; }
.ulListDown { padding: 5rem; display: flex; justify-content: left; flex-wrap: wrap; width: 80rem; margin: 0 auto; }
.selectDate { display: flex; justify-content: center; margin-top: 3rem; }
.arrowCenter { margin: 0 4rem; font-size: 2.4rem; }
.arrowLeft, .arrowRight { font-size: 2.4rem; font-weight: 300; }
.pagelist { display: flex; justify-content: center; margin-bottom: 5rem; }
.season { display: flex; justify-content: center; width: 65rem; margin: 3rem auto;   }
.recommend { display: flex; flex-wrap: wrap; justify-content: left; width: 65rem; height: 43rem; overflow: hidden; margin: 0 auto; }
.down { display: flex; flex-wrap: wrap; justify-content: left; width: 65rem; margin: 0 auto; }
.plusButton, .minusButton { width: 100%; text-align: center; font-size: 1.6rem; color: violet; margin-top: 1rem; margin-bottom: 3rem; }